.profile-input {
  border: 0;
  font-family: "Comfortaa", cursive;
  font-size: 16px;
  color: #999999;
  background-color: #fff;
  text-align: end;
}

.profile-input:focus {
  outline: none;
}

.login-input {
  padding: 10px;
  margin: 5px 0;
  font-family: "Comfortaa", cursive;
  font-size: large;
  border: 0;
  border-bottom: 1px solid black;
}

.profile-editable-input {
  border: 0;
  padding: 5px;
  font-family: "Comfortaa", cursive;
  font-size: 16px;
  background-color: #fff;
  text-align: end;
}

.search-textinput {
  padding: 10px;
  width: 100%;
  margin-left: 30px;
  margin-right: 30px;
  background-color: #f9f9f9;
  font-family: "Comfortaa", cursive;
  font-size: large;
  border: 0;
  border-bottom: 1px solid black;
  background: url(../static/magnifying-glass-solid.svg) no-repeat 9px 9px;
  background-size: 20px;
  padding-left: 40px;
}

.new-message-textinput {
  padding: 20px;
  width: calc(100% - 60px);
  margin-left: 30px;
  margin-right: 30px;
  background-color: #fff;
  font-family: "Comfortaa", cursive;
  font-size: large;
  border: 0;
  border-top: 1px solid black;
}
